.postContainer{
    border: 2px solid blue;
    background-color: black;
    color: white;
    margin: 1rem;
    /* width: 40rem; */
}

.postContainer:hover{
    transition: 0.5s all ease-in-out;
    box-shadow: 0 0 20px rgb(245 120 255 / 60%);
    /* border: 0; */
}

.userInfo{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    font-weight: 500;
    padding: 0 0.5rem;
}

.infoAndButtons{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin: 0.5rem;
}

.buttons a{
    margin: 0 0.5rem 0 0.5rem;
}

.likeButton{
    transition: 0.3s ease-in-out;
}

.likeButton:hover{
    transform: scale(1.3);
}

.addComment, .showCommentsContainer {
    transition: max-height 0.5s ease-in-out, opacity 0.5s ease-in-out;
    overflow: hidden;
    max-height: 0;
    opacity: 0;
}

.addComment.show, .showCommentsContainer.show {
    max-height: 1000px; 
    opacity: 1;
}

.addComment.hidden, .showCommentsContainer.hidden {
    transition: max-height 0.5s ease-in-out, opacity 0.5s ease-in-out;
    max-height: 0;
    opacity: 0;
}

/* post button css  */
.btnPrimary {
    background-color: #007bff;
    border: none;
    color: white;
    padding: 10px 20px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    margin: 4px 2px;
    cursor: pointer;
    border-radius: 12px;
    transition: background-color 0.3s, transform 0.3s;
}

.btnPrimary:hover {
    background-color: #0056b3;
    transform: scale(1.05);
}

.btnPrimary:active {
    background-color: #004494;
    transform: scale(0.95);
}

.btnPrimary:focus {
    outline: none;
    box-shadow: 0 0 5px rgba(0, 123, 255, 0.5);
}
