.container{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-around;
    margin: 0.5rem;
}

.card{
    background-color: black;
    color: white;
}

.post{
    width: 18rem;
    border: 2px solid grey;
    border-radius: 10px;
    color: white;
    transition: all 0.5s ease-in-out;
    margin: 0.5rem;
}

.post:hover{
    box-shadow: 0 0 10px 1px violet;
    transform: scale(1.05);
}

.postImage{
    height: 15rem;
    object-fit: contain;
    margin: 0.5rem;
}

.actions{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
}


