.container {
    display: flex;
    justify-content: space-between;
}

.column1 {
    width: 20rem;
    padding: 1rem;
    border-right: 1px solid white;
    border-radius: 10px;
    background: linear-gradient(135deg, #0d0d0d, #1a1a1a, #2e2e2e);
    min-height: 100vh;
}

.column2 {
    width: 40rem;
}

.column3 {
    padding: 1rem;
    width: 15rem;
    border-left: 1px solid white;
    border-radius: 10px;
    background: linear-gradient(135deg, #0d0d0d, #1a1a1a, #2e2e2e);
    min-height: 100vh;
}

.stickyComponents {
    position: fixed;
    display: flex;
    flex-direction: column;
}

.logo {
    height: 10rem;
    width: 18rem;
    display: flex;
    justify-content: center;
    transition: all 0.5s ease-in-out;
}

.logo img {
    height: 100%;
}

.logo:hover {
    transform: scale(1.1);
}

.createPost {
    display: flex;
    align-items: center;
    margin: 0.5rem 0;
}

.userProfile {
    margin: 0.5rem 0;
}

.manageButton {
    margin: 0.5rem 0;
}

.clickable {
    display: flex;
    flex-direction: row;
    align-items: center;
    position: relative;
    display: inline-block;
    border-top-right-radius: 50px;
    border-bottom-right-radius: 50px;
    overflow: hidden;
    cursor: pointer;
    transition: 0.5s all ease-in-out;
}

.clickable:hover {
    transform: translateX(0.5rem);
}

.profileLink {
    text-decoration: none;
    color: inherit;
    transition: 0.5s all ease-in-out;
    position: relative;
    overflow: hidden;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
}

.profileLink h2 {
    margin: 0;
}

.icon {
    display: flex;
    height: 100%;
    font-size: 1.5rem;
    margin: 0.5rem;
}

.manageButton {
    color: white;
    display: flex;
    flex-direction: row;
    align-items: center;
}

/* Placeholder Box Styles */
.placeholderBox{
    margin: 1rem;
}

.headerHolder{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.userNameHolder{
    display: flex;
    flex-direction: row;
    align-items: center;
}

.profileHolder{
    border-radius: 50px;
    height:2rem;
    width: 2rem;
    margin: 0.5rem;
}

.nameHolder{
    height: 1rem;
    width: 10rem;
}

.followHolder{
    margin: 0.5rem;
}

/* Column 2 specific styles */

.greetings {
    height: 5rem;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 2px solid violet;
    background: linear-gradient(to left top, #FF7E5F, #FFB88C);
    border-radius: 10px;
    margin: 1rem;
    transition: transform 0.3s ease-in-out, background 1s ease-in-out;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    position: relative;
    overflow: hidden;
}

.greetings h2 {
    font-size: 1.5rem;
    color: white;
    z-index: 1;
}

.greetings::before {
    content: '';
    position: absolute;
    top: -50%;
    left: -50%;
    width: 200%;
    height: 200%;
    background: radial-gradient(circle at center, #FFB88C, #FF7E5F);
    z-index: 0;
    transition: all 1s ease-in-out;
    transform: rotate(45deg);
}

.greetings:hover::before {
    background: radial-gradient(circle at center, violet, yellow);
}

.greetings:hover {
    transform: scale(1.05);
}

.greetings h2::after {
    content: 'âœ¨';
    animation: sparkle 1.5s infinite;
    display: inline-block;
    margin-left: 0.5rem;
}

@keyframes sparkle {
    0% { opacity: 1; transform: scale(1); }
    50% { opacity: 0.5; transform: scale(1.5); }
    100% { opacity: 1; transform: scale(1); }
}

.following {
    margin: 0.5rem;
}

.followers {
    margin: 0.5rem;
}

